/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}
@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 480px) {
  html {
    font-size: 46%;
  }
}

body {
  min-height: 100%;
  background-color: #f9f9f9;
  color: #000000;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}

.container-usm {
  width: 60%;
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin: 5rem auto;
  margin-top: 12rem;
}
@media (max-width: 768px) {
  .section {
    margin-top: 5rem;
  }
}

a {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 900;
  color: #000000;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2rem;
}

p {
  color: #000000;
  font-size: 2.6rem;
}

.alert {
  display: block;
  margin: 1rem auto;
  padding: 1rem;
  color: #ffffff;
  font-weight: 900;
  font-size: 1rem;
  text-transform: uppercase;
  max-width: fit-content;
  border-radius: 1rem;
}
.alert.error {
  background-color: #e74c3c;
  border-left: 0.5rem solid #d62c1a;
}
.alert.success {
  background-color: #50a845;
  border-left: 0.5rem solid #3f8436;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  max-height: 100vh; /* Establece la altura mínima al 100% del viewport */
  margin: 0; /* Elimina el margen predeterminado del body */
  padding: 0;
  background: #e3f2fd;
  z-index: 2;
}

nav {
  position: sticky;
  top: 0;
  left: 0;
  height: 70px;
  width: 100%;
  display: flex;
  align-items: center;
  background: #fff;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
}

nav .logo {
  display: flex;
  align-items: center;
  margin: 0 24px;
}

.logo .menu-icon {
  color: #333;
  font-size: 24px;
  margin-right: 14px;
  cursor: pointer;
}

.logo .logo-name {
  color: #333;
  font-size: 22px;
  font-weight: 500;
}

nav .sidebar {
  position: fixed;
  top: 0;
  left: -100%;
  height: 100%;
  width: 260px;
  padding: 20px 0;
  background-color: #fff;
  box-shadow: 0 5px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.4s ease;
}

nav.open .sidebar {
  left: 0;
}

.sidebar .sidebar-content {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px 16px;
}

.sidebar-content .list {
  list-style: none;
}

.list .nav-link {
  display: flex;
  align-items: center;
  margin: 8px 0;
  padding: 14px 12px;
  border-radius: 8px;
  text-decoration: none;
}

.lists .nav-link:hover {
  background-color: #4070f4;
}

.nav-link .icon {
  margin-right: 14px;
  font-size: 20px;
  color: #707070;
}

.nav-link .link {
  font-size: 16px;
  color: #707070;
  font-weight: 400;
}

.lists .nav-link:hover .icon,
.lists .nav-link:hover .link {
  color: #fff;
}

.overlay {
  position: fixed;
  top: 0;
  left: -100%;
  height: 1000vh;
  width: 200%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s ease;
  background: rgba(0, 0, 0, 0.3);
}

nav.open ~ .overlay {
  opacity: 1;
  left: 260px;
  pointer-events: auto;
}

.main-part {
  width: 80%;
  margin: 0 auto;
  text-align: center;
  padding: 0px 5px;
  margin-top: 50px;
}

.table-container {
  max-width: 1100px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .table-container {
    width: 100%;
  }
}

.content-table {
  border-collapse: collapse;
  margin: 25px auto;
  font-size: 0.9em;
  min-width: 400px;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}
.content-table thead tr {
  background-color: #0b7abb;
  color: #ffffff;
  text-align: left;
  font-weight: bold;
}
.content-table th,
.content-table td {
  padding: 12px 15px;
}
.content-table tbody tr {
  border-bottom: 1px solid #dddddd;
}
.content-table tbody tr:nth-of-type(even) {
  background-color: #ffffff;
}
.content-table .actions-buttons-container {
  display: flex;
  column-gap: 1rem;
  align-items: center;
}
.content-table .actions-buttons-container .actions-button {
  padding: 1rem;
  color: #ffffff;
  border-radius: 1rem;
}
.content-table .actions-buttons-container .actions-button.edit-button {
  background-color: #0b7abb;
}
.content-table .actions-buttons-container .actions-button.delete-button {
  background-color: #e74c3c;
}

@media (max-width: 768px) {
  .content-table tr {
    display: flex;
    flex-direction: column;
    text-align: start;
  }
  .content-table td[data-title] {
    display: flex;
  }
  .content-table td[data-title]::before {
    width: 40%;
    content: attr(data-title);
    font-weight: bold;
  }
  .content-table thead {
    display: none;
  }
}
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.content {
  margin: auto;
  width: 330px;
  padding: 40px 30px;
  background: #dde1e7;
  border-radius: 10px;
  box-shadow: -3px -3px 7px rgba(255, 255, 255, 0.4509803922), 2px 2px 5px rgba(94, 104, 121, 0.288);
}

.content .text {
  font-size: 33px;
  font-weight: 600;
  margin-bottom: 35px;
  color: #595959;
}

.content .text h3 {
  font-size: 28px; /* Tamaño de fuente deseado */
  color: rgba(42, 45, 51, 0.288); /* Color deseado */
  font-weight: bold; /* Grosor de fuente deseado */
  margin-bottom: 5px; /* Espacio inferior deseado */
}

.field {
  height: 50px;
  width: 100%;
  display: flex;
  position: relative;
  margin-top: 20px;
}

.field:nth-child(2) {
  margin-top: 20px;
}

.field input {
  height: 100%;
  width: 100%;
  padding-left: 45px;
  outline: none;
  border: none;
  font-size: 18px;
  background: #dde1e7;
  color: #595959;
  border-radius: 25px;
  box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px rgba(255, 255, 255, 0.4509803922);
}

.field input:focus {
  box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px rgba(255, 255, 255, 0.4509803922);
}

.field span {
  position: absolute;
  color: #595959;
  width: 50px;
  line-height: 50px;
}

.field label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 45px;
  pointer-events: none;
  color: #666666;
}

.field input:valid ~ label {
  opacity: 0;
}

.button {
  margin-top: 20px;
  width: 100%;
  height: 50px;
  font-size: 18px;
  line-height: 50px;
  font-weight: 600;
  background: #dde1e7;
  border-radius: 25px;
  border: none;
  outline: none;
  cursor: pointer;
  color: #595959;
  box-shadow: 2px 2px 5px #BABECC, -5px -5px 10px rgba(255, 255, 255, 0.4509803922);
}

.button:focus {
  color: #3498db;
  box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px rgba(255, 255, 255, 0.4509803922);
}

.cpanel-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
@media (max-width: 768px) {
  .cpanel-container {
    flex-direction: column;
    row-gap: 4rem;
  }
  .cpanel-container .cpanel {
    width: 100%;
  }
}

.cpanel {
  width: 32%;
  background-color: #434343;
  color: #ffffff;
  min-height: 20rem;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.icon-part {
  flex: 3;
}

.card-content-part {
  flex: 1;
}

.icon-part i {
  font-size: 30px;
  padding: 10px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  margin-top: -25px;
  margin-bottom: 10px;
  background-color: #434343;
}

.icon-part p {
  margin: 0px;
  font-size: 20px;
  padding-bottom: 10px;
}

.card-content-part {
  background-color: #2f4254;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cpanel .card-content-part:hover {
  background-color: #565758;
  cursor: pointer;
}

.card-content-part a {
  color: #ffffff;
  text-decoration: none;
  padding: 10px 0;
  width: 100%;
  height: 100%;
}

.cpanel-green .icon-part,
.cpanel-green .icon-part i {
  background-color: #16a085;
}

.cpanel-green .card-content-part {
  background-color: #107360;
}

.cpanel-orange .icon-part,
.cpanel-orange .icon-part i {
  background-color: #f39c12;
}

.cpanel-orange .card-content-part {
  background-color: #c87f0a;
}

.cpanel-blue .icon-part,
.cpanel-blue .icon-part i {
  background-color: #0b7abb;
}

.cpanel-blue .card-content-part {
  background-color: #085b8b;
}

.cpanel-red .icon-part,
.cpanel-red .icon-part i {
  background-color: #e74c3c;
}

.cpanel-red .card-content-part {
  background-color: #d62c1a;
}

.cpanel-skyblue .icon-part,
.cpanel-skyblue .icon-part i {
  background-color: #8e44ad;
}

.cpanel-skyblue .card-content-part {
  background-color: #703688;
}